<template>
  <div v-if="show" class="modal-overlay" @click.self="closeModal">
    <div class="modal-container do-invert">
      <div class="modal-header">
        <h2>
          <i class="fas fa-coins"></i>
          Buy $BITCAT
        </h2>
        <button class="close-btn" @click="closeModal">
          <i class="fas fa-times"></i>
        </button>
      </div>
      
      <div class="modal-content">
        <div class="terminal-info">
          <p>Swap any token for $BITCAT using <a href="https://jup.ag/swap/SOL-4j9bDg7iWNah1Qa61rrqwWZMtEdqV3fV56SzyhfNpump" target="_blank">Jupiter</a></p>
        </div>
        
        <!-- Jupiter Terminal Container -->
        <div id="integrated-terminal" class="terminal-container"></div>
        
        <div class="modal-footer">
          <div class="warning-text">
            <i class="fas fa-exclamation-triangle"></i>
            <span>Verify transaction details before confirming</span>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script setup>
import { ref, watch, nextTick, onMounted } from 'vue';
import { useWallet } from 'solana-wallets-vue';

const props = defineProps({
  show: {
    type: Boolean,
    default: false
  }
});

const emit = defineEmits(['close']);

const walletProps = useWallet();
let jupiterTerminal = null;

// Wait for Jupiter script to load
const waitForJupiter = () => {
  return new Promise((resolve) => {
    if (window.Jupiter) {
      resolve();
    } else {
      const checkJupiter = () => {
        if (window.Jupiter) {
          resolve();
        } else {
          setTimeout(checkJupiter, 100);
        }
      };
      checkJupiter();
    }
  });
};

// Initialize Jupiter Terminal
const initializeTerminal = async () => {
  if (typeof window !== "undefined" && props.show) {
    try {
      // Wait for Jupiter to be available
      await waitForJupiter();
      
      // Check if there's an existing instance to resume
      if (window.Jupiter._instance) {
        console.log('Resuming existing Jupiter instance...');
        window.Jupiter.resume();
        return;
      }
      
      // Clear any existing terminal
      const container = document.getElementById('integrated-terminal');
      if (container) {
        container.innerHTML = '';
      }
      
      console.log('Initializing Jupiter Terminal...');
      jupiterTerminal = window.Jupiter.init({
        endpoint: "https://sleek-icy-frog.solana-mainnet.quiknode.pro/9d4845e2d4c60caf45dc4f10d0bdcfdbcd6a321c/",
        displayMode: "integrated",
        integratedTargetId: "integrated-terminal",
        formProps: {
          initialInputMint: "So11111111111111111111111111111111111111112",
          initialOutputMint: "4j9bDg7iWNah1Qa61rrqwWZMtEdqV3fV56SzyhfNpump",
          fixedMint: "4j9bDg7iWNah1Qa61rrqwWZMtEdqV3fV56SzyhfNpump",
          swapMode: "ExactInOrOut"
        },
        branding: {
            logoUri: "https://bitcat.fun/data/smallcat.png",
            name: "BITCAT"
        }
      });
      console.log('Jupiter Terminal initialized successfully');
    } catch (error) {
      console.error('Failed to initialize Jupiter Terminal:', error);
    }
  }
};

// Clean up terminal
const cleanupTerminal = () => {
  if (jupiterTerminal) {
    try {
      // Clear the container
      const container = document.getElementById('integrated-terminal');
      if (container) {
        container.innerHTML = '';
      }
      jupiterTerminal = null;
    } catch (error) {
      console.error('Error cleaning up terminal:', error);
    }
  }
};

// Close modal
const closeModal = () => {
  cleanupTerminal();
  // Close Jupiter widget
  if (window.Jupiter) {
    try {
      window.Jupiter.close();
    } catch (error) {
      console.error('Error closing Jupiter:', error);
    }
  }
  emit('close');
};

// Watch for modal show/hide
watch(() => props.show, async (newValue) => {
  if (newValue) {
    await nextTick();
    setTimeout(initializeTerminal, 100);
  } else {
    cleanupTerminal();
  }
});

// Handle ESC key
const handleKeydown = (event) => {
  if (event.key === 'Escape' && props.show) {
    closeModal();
  }
};

onMounted(() => {
  document.addEventListener('keydown', handleKeydown);
});
</script>

